import { observable, action, makeObservable } from "mobx";

class AppStore {
  balance = 0;
  counter = 0;
  progress = 1000;
  showPage = false;

  constructor() {
    makeObservable(this, {
      balance: observable,
      counter: observable,
      progress: observable,
      showPage: observable,
      updateBalance: action,
      updateCounter: action,
      updateProgress: action,
      updateShowPage: action,
    });

    this.startProgressUpdate();
  }

  updateBalance(amount) {
    if (this.balance + amount >= 0) {
      this.balance += amount;
    } else {
      this.balance = 0;
    }
  }

  updateCounter(amount) {
    if (this.counter + amount >= 0) {
      this.counter += amount;
    } else {
      this.counter = 0;
    }
  }

  updateProgress(amount) {
    if (this.progress + amount >= 0 && this.progress + amount <= 1000) {
      this.progress += amount;
    } else if (this.progress + amount < 0) {
      this.progress = 0;
    }
  }

  updateShowPage(item) {
    this.showPage = item;
  }

  startProgressUpdate() {
    setInterval(() => {
      this.updateProgress(1);
    }, 1000);
  }
}

const appStore = new AppStore();
export default appStore;
