import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import WebApp from "@twa-dev/sdk";
import { BackButton } from "@twa-dev/sdk/react";
import appStore from "../../store";
import { observer } from "mobx-react";

const Frens = observer(() => {
  const navigate = useNavigate();
  return (
    <>
      <BackButton onClick={() => navigate("/")} />
      <div className="relative">
        <div className="GE4s JHGD"></div>
        <div className="Kh3x relative ScRl">
          <div className="min-h-[101vh] pt-[16px] pb-[80px]">
            <div className="mt-[24px] flex justify-center items-center">
              <div className="text-[32px] font-semibold text-white">
                0 Frens
              </div>
            </div>
            {/* <div class="flex justify-center items-center"><button class="KH5k">How it works</button></div> */}
            <div className="mt-[36px] pl-[14px]">
              <div className="text-[18px] font-semibold leading-[24px]">
                How it works
              </div>
              <div className="mt-[10px]">
                <div className="items-stretch flex gap-[20px]">
                  <div className="HGqp">
                    <div className="bg-white h-full w-[2px] invisible"></div>
                    <div className="bg-white rounded-[10px] h-[10px] w-[10px]"></div>
                    <div className="bg-white h-full w-[2px]"></div>
                  </div>
                  <div className="py-[16px]">
                    <div className="text-[16px] font-medium leading-[20px]">
                      Share your invitation link
                    </div>
                    <div className="text-[#ddd] text-[14px] font-normal leading-[20px] mt-[4px]">
                      Spread the crypto among friends
                    </div>
                  </div>
                </div>
                <div className="items-stretch flex gap-[20px]">
                  <div className="HGqp">
                    <div className="bg-white h-full w-[2px]"></div>
                    <div className="bg-white rounded-[10px] h-[10px] w-[10px]"></div>
                    <div className="bg-white h-full w-[2px]"></div>
                  </div>
                  <div className="py-[16px]">
                    <div className="text-[16px] font-medium leading-[20px]">
                      Your friends join Bitcoin
                    </div>
                    <div className="text-[#ddd] text-[14px] font-normal leading-[20px] mt-[4px]">
                      And start farming crypto
                    </div>
                  </div>
                </div>
                <div className="items-stretch flex gap-[20px]">
                  <div className="HGqp">
                    <div className="bg-white h-full w-[2px]"></div>
                    <div className="bg-white rounded-[10px] h-[10px] w-[10px]"></div>
                    <div className="bg-white h-full w-[2px] invisible"></div>
                  </div>
                  <div className="py-[16px]">
                    <div className="text-[16px] font-medium leading-[20px]">
                      Earn 10% of the BTC farmed by frens
                    </div>
                    <div className="text-[#ddd] text-[14px] font-normal leading-[20px] mt-[4px]">
                      Let's farm together!
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="px-[14px]">
              <div className="text-[22px] font-semibold mt-[50px] text-white">
                Frens List
              </div>
            </div>
            <div className="px-[14px] mt-[14px]">
              <div class="rounded-[16px] bg-[rgba(255,255,255,.12)] text-center">
                <div className="text-[98px] pt-[40px]">🧸</div>
                <h1 class=" whitespace-nowrap text-[14px] font-medium text-[rgba(235,235,245,.3)] pt-[14px] pb-[40px]">
                  No frens Yet
                </h1>
              </div>
            </div>
            <button className="SD3x">
              <div className="flex justify-center items-center">
                <div className="flex items-center">
                  <div className="text-white text-[17px] font-semibold">
                    Invite a fren
                  </div>
                </div>
              </div>
            </button>
          </div>
        </div>
      </div>
    </>
  );
});

export default Frens;

