import "./style.scss";
import { useState, useEffect } from "react";
import appStore from "../../store";

export default function ProgressBar() {
    return(
        <div className="progress-bar">
            <div className="progress-bar-line progress-bar-color_type-bronze" style={{
                width: `${(appStore.progress / 1000) * 100}%`,
            }}>
            </div>
        </div>
    )
}